*{
  /* box-sizing: border-box; */
  margin: 0px;
  padding: 0px;
  background-color: aliceblue;
  font-family: sans-serif;
}
div{
  width: 100%;
  height: 100vh;
  background-color: rgb(24, 124, 78);
  /* display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; */
}
h1 {
  color: white;
  background: transparent;
  text-shadow: 0 2px 2px black;
  text-align: center;
}
p{
  color: white;
  background: transparent;
  text-shadow: 0 2px 2px black;
  text-align: center;
}
input{
  width: 40%;
  padding: 10px 20px;
  border: none;
  outline: none;
  margin: 20px 0;
  text-align: center;
  margin-left: 50%;
  
}

button{
  display: flex;
  line-height: 24px;
  padding: 0 20px;
  background: plum;
  color: wheat;
  border: 2px solid palegreen;
  outline: none;
  border-radius: 5px;
  text-transform: uppercase;
  cursor: pointer;
  margin-left: 650px;
  width: 80px;
  text-align: center;
}